import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { LineMessageServicesProvider } from "../../providers/line-message-services/line-message-services";
import { ModalController, LoadingController } from "ionic-angular";
import { LineMessageViewModel } from "../../Model/ViewModel/LineMessageViewModel";
/**
 * Generated class for the LineMessageAddEditModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-line-message-add-edit-modal",
  templateUrl: "line-message-add-edit-modal.html"
})
export class LineMessageAddEditModalPage {
  item: any;
  mode: string;
  change_mode: string;
  line_url: string;
  title: string = "Token訊息";
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public LineMessageServices: LineMessageServicesProvider,
    public loadingCtrl: LoadingController,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.title = navParams.data.item.tokenname;
    this.item = new LineMessageViewModel();
    this.item.tokenname = navParams.data.item.tokenname;
    if (this.change_mode === "c")
      this.item.subject_no = navParams.data.item.subject_no + "_copy";
    else this.item.subject_no = navParams.data.item.subject_no;
    this.item.subject = navParams.data.item.subject;
    this.item.message = navParams.data.item.message;

  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad LineMessageAddEditModalPage");
  }
}



